var debug = require('../utils/debug');
var registerComponent = require('../core/component').registerComponent;
var THREE = require('../lib/three');

var warn = debug('components:mmd-model:warn');

module.exports.Component = registerComponent('mmd-model', {

  schema: {
    vmd: {type: 'src'},
    vpd: {type: 'src'},
    obj: {type: 'src'}
  },

  init: function () {
    this.model = null;
    this.mmdLoader = new THREE.MMDLLoader();
  },

  update: function () {
    var data = this.data;
    if (!data.obj) { return; }
    this.remove();
    this.loadObj(data.obj, data.vpd);
  },

  remove: function () {
    if (!this.model) { return; }
    this.el.removeObject3D('mesh');
  },

  loadObj: function (objUrl, vpdUrl) {
    var self = this;
    var el = this.el;
    var mmdLoader = this.mmdLoader;

    mmdLoader.loadModel(objUrl, function (model) {
      self.model = model;
      el.setObject3D('mesh', model);
      el.emit('model-loaded', {format: 'mmd', model: model});
    });
  }
});
